.Mission-Title{
    margin-top: 5%;
    width: 70%;
    margin-left: 15%;
    text-align: right;
    letter-spacing: 0;
    opacity: 1;
}

.container{
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
}

.Mission-Paragraph{
    margin-top: 5%;
    width: 85%;
    text-align: right;
    letter-spacing: 0;
    opacity: 1;
}

.Support-Link{
    margin-top: 5%;
    padding-left: 10%;
    width: 85%;
    text-align: right;
    letter-spacing: 0;
    opacity: 1;
}

.Dancers-Image{
    margin-top: 5%;
    width: 99%;
    height: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.column1{
    padding-left: 6%;
}

.column2{
    padding-right: 6%;
}

.Mobile{
    display: none;
}

@media only screen and (max-width: 600px) {
    .Mission-Title{
        width: 70%;
        text-align: center;
    }

    .Mission-Paragraph{
        text-align: left;
        margin-top: 5%;
        margin-left: 7%;
        width: 86%;
    }
    .Support-link{
        text-align: left;
        margin-top: 5%;
        margin-left: 7%;
        width: 86%;
    }

    .Dancers-Image{
        margin-top: 5%;
        width: 100%;
        height: auto;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }

    .Web{
        display: none;
    }
    
    .Mobile{
        display: block;
    }
}

